:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.load-more-section {
  margin-top: 5px;
}

.load-more-section, .search-section {
  background-color: #c5e2ff;
  justify-content: center;
  display: flex;
}

.load-more {
  cursor: pointer;
  background: #d9ecff;
  border: none;
  border-radius: 2px;
  outline: none;
  padding: 8px 16px;
  display: none;
  position: relative;
}

.load-more, .search-form {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 5px 30px;
}

.button__text {
  color: #fff;
  font: bold 16px Quicksand, san-serif;
  transition: all .2s;
}

.button--loading:after {
  content: "";
  width: 14px;
  height: 14px;
  border: 4px solid rgba(0, 0, 0, 0);
  border-top-color: #fff;
  border-radius: 50%;
  margin: auto;
  animation: button-loading-spinner 1s infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

